@import url(https://fonts.googleapis.com/css2?family=Muli:wght@400;700&display=swap);
:root{--mainBg: #f9f9f9;--blue: #235bf6;--fontColor: #242e4c}*,*:before,*:after{box-sizing:border-box;margin:0;padding:0;font-family:"Muli", sans-serif;color:#242e4c;color:var(--fontColor)}img{width:100%}body{background:#f9f9f9;background:var(--mainBg);min-height:100vh}button{cursor:pointer}a{text-decoration:none}.day{--size: 100px;position:absolute;right:15px;top:93px;width:var(--size);height:var(--size);background:white;display:flex;justify-content:center;align-items:center;flex-direction:column;box-shadow:0 2px 10px rgba(36,46,76,0.1)}.day p{color:14px;text-transform:uppercase;font-weight:bold;opacity:0.5}.day h4{font-size:30px}@media (max-width: 783px){.day{display:none}}.page{padding:3em 0;display:flex;flex-direction:column;place-items:center}.page>h1{font-size:24px;margin-bottom:1em}.page>p{width:70%;max-width:700px;font-size:16px;text-align:center;line-height:1.7;margin-bottom:2em;opacity:0.75}header{display:grid;place-items:center;height:76px;width:100%;background:white;box-shadow:0 2px 10px rgba(36,46,76,0.1);font-size:14px;font-weight:bold}header ul{display:grid;grid-template-columns:repeat(3, minmax(20px, 1fr));grid-gap:2em;gap:2em}header ul a{opacity:0.5;transition:0.2s ease;position:relative;text-align:center;padding:0.5em}header ul a:before{content:"";position:absolute;left:0;bottom:-1px;height:2px;width:100%;background:#242e4c;background:var(--fontColor);transform:scaleX(0);transition:0.2s ease}header ul a.active{opacity:1}header ul a.active:before{transform:scaleX(0.2)}header ul a:hover{opacity:1}header ul a:hover:before{transform:scaleX(1)}.projects>a{color:white;background:#235bf6;background:var(--blue);border:none;padding:1em 3em;font-size:16px;border-radius:500px;margin-bottom:3em}.projects .projects-container{display:grid;grid-template-columns:repeat(auto-fit, minmax(280px, 1fr));grid-gap:2em;gap:2em;width:70%}.projects .projects-container .project{background:white;padding:1em;position:relative;transition:0.2s ease;box-shadow:0 2px 10px rgba(36,46,76,0.1)}.projects .projects-container .project img{object-fit:contain}.projects .projects-container .project .info{position:absolute;left:0;top:0;width:100%;height:100%;background:white;padding:1em;display:flex;flex-direction:column;transform:scaleY(0);transform-origin:top;transition:0.2s ease-in-out}.projects .projects-container .project .info h4,.projects .projects-container .project .info p,.projects .projects-container .project .info .buttons{transform:scaleY(0);transform-origin:top}.projects .projects-container .project .info h4{margin-bottom:0.5em;transition:0.2s ease 0.3s}.projects .projects-container .project .info p{opacity:0;transition:0.2s ease 0.5s}.projects .projects-container .project .info .buttons{display:flex;justify-content:space-between;margin-top:auto;transition:0.2s ease 0.7s}.projects .projects-container .project .info .buttons a{position:relative;padding:0.3em;display:flex;justify-content:center;align-items:center;flex-direction:column}.projects .projects-container .project .info .buttons a:after{content:"";position:absolute;left:0;bottom:0;width:100%;height:2px;transform:scaleX(0.3);transition:0.2s ease;background:#242e4c;background:var(--fontColor)}.projects .projects-container .project .info .buttons a:hover:after{transform:scaleX(0.8)}.projects .projects-container .project:hover{box-shadow:0 2px 10px rgba(36,46,76,0.2);transform:scale(1.01)}.projects .projects-container .project:hover .info{transform:scaleY(1)}.projects .projects-container .project:hover .info h4,.projects .projects-container .project:hover .info p,.projects .projects-container .project:hover .info .buttons{opacity:1;transform:scaleY(1)}.contact form{display:grid;grid-template-columns:repeat(2, 1fr);font-size:13px;grid-gap:1em;gap:1em;width:70%;max-width:600px}.contact form .form-control input,.contact form .form-control textarea{width:100%;border:1px solid #ccc;padding:0.5em;font-size:1em;resize:none}.contact form .form-control input.error,.contact form .form-control textarea.error{border:1px solid red}.contact form .form-control textarea{height:100px}.contact form .form-control:nth-child(3){grid-column:1 / -1}.contact form .form-control .error{color:red;opacity:0.7}.contact form button{grid-column:1/ -1;background:#235bf6;background:var(--blue);color:white;border:none;padding:1em;font-size:1em}.contact form .message{grid-column:1 / -1;text-align:center;padding:1em;font-size:1em;border:1px solid #242e4c;border:1px solid var(--fontColor);font-weight:bold}.about .skills-container{display:grid;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));width:80%;grid-gap:1em;gap:1em}.about .skills-container>div{padding:1em;border:10px solid #242e4c;border:10px solid var(--fontColor);background:white}.about .skills-container>div>h2{display:flex;flex-direction:column;place-items:center;margin-bottom:1em;line-height:1.8}.about .skills-container ul{list-style-type:none}@media (max-width: 600px){form{grid-template-columns:1fr !important;width:90% !important}}

